.previewStepper-main{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;


    .preview-arrow-buttons{
        display: flex;
    }

    .preview-steps{

        font-size: 18px;
        font-weight: bold;
    }

    .arrow-div{
        height: 50px;
        width: 50px;
        border-radius: 50%;
        background: #04724d;
        color: #fff;
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 0 20px;
        cursor: pointer;

    }
    .arrow-div-disabled{
        height: 50px;
        width: 50px;
        border-radius: 50%;
        background: #bfbfbf;
        color: #fff;
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 0 20px;

    }
}


