.mcq-options-card-main{
    border: 2px solid #ddd;
    border-radius: 4px;
    background: #fff;

    .options-heading-div{
        border-bottom: 2px solid #ddd;
    }

    .options-heading{
        margin: 10px 20px;
        font-weight: 600;
        color: #000;
      
    }

    .options-body{
        padding: 10px;
    }

    .add-option{
        display: flex;
        padding: 10px 0;
        align-items: center;
        .add-option-circle{
        height: 33px;
        width: 33px;
        display: flex;
        justify-content: center;
        align-items: center;
        background: #21745D;
        border-radius: 50%;
        cursor: pointer;
        }

        .add-option-text{
            font-size: 13px;
            margin: 0 10px;
            font-weight: 500;

        }
    }

    
}