.top-wrapper{
  padding: 5px 10px ;
  border-bottom: 1px solid #bdbdbd ;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.close{
    height: 30px;
    width: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
  


}
.dialog-main-heading {
    font-weight: 600;
    font-size: 18px;
  }
  
  .dialog-sub-heading {
    font-size: 12px;
    color: #333;
    padding: 5px 0;
    opacity: 0.8;
    font-weight: 600;
  }
  .dialog-action-1-class {
    font-size: 14px;
    font-weight: 600;
    border-radius: 4px;
    border: 2px solid #bdbdbd;
    padding: 5px 20px;
    // margin-right: 10px;
    color: #04724d;
    cursor: pointer;
  }
  
  .dialog-action-2-class {
    font-size: 14px;
    font-weight: 600;
    border-radius: 4px;
    padding: 5px 20px;
    background-color: #04724d;
    color: #fff;
    cursor: pointer;
  }
.icon-heading{
    display: flex;
}

.dialog-body{
    padding: 10px 20px;
}