.mcq-wrapper {
  margin: 30px 40px 40px 40px;
  padding: 20px;
}
.list-card-main {
  background: #f1f3f6;
  padding: 8px 25px 25px 25px;
  border-radius: 4px;

  .list-card-heading {
    font-size: 24px;
    font-weight: bold;
    color: #000;
    padding-bottom: 20px;
  }
}
.res-btn:hover {
  cursor: pointer;
}

.res-btn {
  margin-bottom: 0 !important;
  background: #04724d;
  border-radius: 4px;
  display: flex;
  justify-content: center;
  color: #fff;
  align-items: center;
  height: 34px;
  font-size: 13px;
  font-family: "Poppins", sans-serif;
}
.filter-by-skils {
  font-size: 12px;
  font-weight: 600;
  margin-right: 5px;
  margin-top: 7px;
}

.searchInputWrap {
  margin-left: 25px;
  width: 75%;
}
.mb {
  margin-bottom: 20px;
  padding-left: 15px;
  padding-right: 10px;
}
.questionSkills .MuiInputBase-root {
  padding: 0 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: auto !important;
  min-height: 34px !important;
  border: 2px solid #ddd;
  border-radius: 4px;
  background: #fff;
  margin-bottom: 10px;
}
.questionSkills .MuiChip-outlined {
  border: 1px solid #333;
  color: #fff;
  background: black !important;
  height: 20px;
  margin: 5px;
}
.MuiChip-outlined .MuiChip-deleteIcon {
  color: #fff;
}

.MuiChip-outlined .MuiChip-deleteIcon:hover {
  color: #fff;
}
.resume-card {
  margin: 30px auto;
  width: 1086px;
  font-size: 16px;
  border-radius: 4px;
  font-family: "Poppins", sans-serif;
}
.load-more-btn {
  cursor: pointer !important;
  border: none;
  background: transparent;
  width: 100%;
  text-decoration: underline;
}
