.list-verbal-question-card-main {
  display: flex;
  justify-content: space-between;
  padding: 5px 10px;
  font-size: 11px;
  border-radius: 4px;
  font-weight: 500;
  align-items: center;
  margin-bottom: 10px;


  .list-verbal-time-delete {
    display: flex;
    width: 30%;
    justify-content: flex-end;
  }

  .list-verbal-text {
    width: 70%;
  
  }

  .list-verbal-time {
    opacity: 0.5;
    padding: 0 10px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .list-verbal-delete {
    height: 25px;
    width: 25px;
    display: flex;
    justify-content: center;
    background: #d8dce2;
    align-items: center;
    border-radius: 50%;
    cursor: pointer;
    margin-right: 10px;
  }

  .skills-tooltip {
    width: 400px !important;
    margin-right: 20px !important;
    min-height: 50px !important;
    padding: 0 !important;
    border-radius: 8px !important;
  }

  .list-tooltip-main {
    .skills-heading {
      color: #000;
      border-bottom: 1px solid #dadada !important;
      padding: 5px 10px !important;
    }
  }
}

.main-skills {
  display: flex;
  flex-wrap: wrap;
  margin: 10px;
}

.chip-div {
  background-color: #000;
  color: #fff;
  padding: 0px 20px;
  border-radius: 25px;
  // margin-right: 5px;
}

