.option-component-main{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5px 10px;
    border: 2px solid #ddd;
    border-radius: 4px;

    .checkbox-input-div{
        display: flex;
       align-items: center;
    //    justify-content: center;
    width: 80%;
    }

    .input-1{
        margin-right: 20px;
        display: flex;
        align-items: center;
        // justify-content: center;
        // width: 20%;
      
    }

    .input-2{
        // width: 80%;
        width: 300px;
        .MuiFormControl-root{
            width: 100%;
        }
    }

    .input-1 label{
        margin-bottom: 0 !important;
    }

    
    .option-delete{
        height: 25px;
        width: 25px;
        display: flex;
        justify-content: center;
        background: #ddd;
        align-items: center;
        border-radius: 50%;
        cursor: pointer;
    }

    .optionValue{
        cursor: pointer;
        font-size: 13px;
        height: 25px;
        display: flex;
        // justify-content: center;
        // align-items: center;
        font-weight: 500;
        color: #000;
        width: 200px;
    }

    .optionValue:hover{
        border-bottom: 1px solid #333;
    }
}




.option-input .MuiInput-underline {
    border: none !important;

}

.option-input .MuiInputBase-root{
    height: 25px !important;
    font-size: 13px !important;
    width: 100% !important;
}

.optionValue .MuiFormControl-root{
    width: 100% !important;
}