.mcq-question-card-main {

  padding: 10px;
  font-size: 13px;
  border-radius: 4px;
  font-weight: 500;
  background: #fff ;
  // align-items: center;

  .mcq-time-delete {
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }

  .option-text{
    font-size: 11px;
   font-family: 'Poppins', sans-serif;
  }

  .mcq-time {
    opacity: 0.5;
    padding: 0 10px;
    font-size: 11px;
   font-family: 'Poppins', sans-serif;
  }

  .mcq-delete {
    height: 25px;
    width: 25px;
    display: flex;
    justify-content: center;
    background: #fff;
    align-items: center;
    border-radius: 50%;
    cursor: pointer;
  }

  .option {
    display: flex;
    margin: 5px 0;
    align-items: center;
    font-size: 11px;
  }

  .mcq-text {
    font-size: 11px;
  }

  .sno {
    padding-right: 10px;
  }
}

.list-verbal-delete {
    height: 25px;
    width: 25px;
    display: flex;
    justify-content: center;
    background: #d8dce2;
    align-items: center;
    border-radius: 50%;
    cursor: pointer;
    margin-right: 10px;
  }