.mcq-CreateMCQ-Main{
    padding: 20px;
    background: #F1F3F6;
    border-radius: 8px;

    .css-ahj2mt-MuiTypography-root{
      font-family: 'Poppins', sans-serif;
      }
  

    .jodit-container .jodit-workplace {
        background: #fff !important;
    }

    .jodit-toolbar-editor-collection_size_middle.jodit-toolbar-editor-collection_mode_horizontal{
        background: #fff !important;
    }


    .mcq-add-question-button{
        background: #fff !important;
        color: #04724d !important;
        border-radius: 4px !important;
        width: 140px !important;
        font-size: 13px !important;
        font-weight: 600;
    }

    .mcq-controlled-radio{
        padding: 10px;
    }

    .mcq-question-area{
        display: flex;
        justify-content: space-between;
        padding-bottom: 10px;
    }

    .mcq-editor{
      width: 49%;
    }
    .mcq-options{
        width: 49%;
    }
    .mcq-question-text-area {
        width: 100% ;
        border: 2px solid #ddd;  
        border-radius: 4px;
        padding: 10px;
        font-size: 13px;
        margin-bottom: 10px;
}

.mcq-question-min-sec{
    display: flex;
    margin-bottom: 10px;
    .mcq-ques-min input{
        width: 60px;
        height: 34px;
        margin-right: 10px;
       text-align: center;
       border: 2px solid #ddd;  
       border-radius: 4px;
       font-size: 13px;
    }
    
    .mcq-ques-sec input{
        width: 60px;
        height: 34px;
        font-size: 13px;
        margin-right: 10px;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 4px;
        text-align: center;
        border: 2px solid #ddd;  
    }

}



.multiSelectContainer {
  text-align: left;
  width: 100%;

}


.multiSelectContainer .MuiAutocomplete-tag {
  padding: 11px;
    margin: 5px;
  }
  
  .MuiAutocomplete-input:first-child {
    padding: 6px 10px !important;
    font-size: 13px;
  }
  .MuiAutocomplete-inputRoot .MuiAutocomplete-input{
    font-size: 13px;
  }

  .multiSelectContainer li:hover {
      background: #04724d !important;
      color: #fff;
      cursor: pointer;
    }

    .MuiInput-underline:before{
        border-bottom: none;
    }

       .MuiInput-underline:before{
        border-bottom: none;
    }







.mcq-add-question-button-2{
