}






}

.mcq-top-filter-clear{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 10px;


  .mcq-filter-by-skils{
    font-size: 13px;
    font-weight: 600;
    margin-right: 10px;
  }

  .mcq-clear-filter{
    display: flex;
    align-items: center;
    

    .mcq-close{
      height: 30px;
      width: 30px;
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer; 
  }
  }
}

